'use client'
import { Archivo_Black } from 'next/font/google'
import React, { useEffect, useState } from 'react'
import { TypeAnimation } from 'react-type-animation'
import LogotypeBanner from 'src/components/logotypeBanner.component'
import { cn } from 'src/lib/utils'

const font = Archivo_Black({
	variable: '--font-sans',
	weight: '400',
})

type indeedStatus = 'constructive' | 'cautionary' | 'destructive'
const commandlineEntryTempl = [
	'Server started on port 3000',
	'Database connected successfully',
	'GET\trequest received at /api/users',
	'POST\trequest received at /api/users',
	'User authenticated successfully',
	'Error\t\tUnable to connect to database',
	'Warning\ttHigh memory usage detected',
	'Info\tServer uptime is 24 hours',
]

const HomePage: React.FC<Readonly<{}>> = () => {
	const [mousePosition, setMousePosition] = useState<{ x: number; y: number }>({
		x: 0,
		y: 0,
	})

	const [commandlineEntries, setCommandlineEntries] = useState<React.ReactNode[]>([])

	const mouseMoveHandler = event => {
		setMousePosition({
			x: event.clientX,
			y: event.clientY,
		})
	}

	const addLog = () => {
		setCommandlineEntries(prevState => {
			const commandlineEntry = commandlineEntryTempl[Math.floor(Math.random() * commandlineEntryTempl.length)]
			let newState = [...prevState]

			const getSequenceOrSelf = entry => (entry.props ? entry.props.sequence[0] ?? entry : entry)

			if (newState.length >= 3) {
				newState = newState.map(getSequenceOrSelf).slice(1)
			}

			newState.push(<TypeAnimation key={commandlineEntry} cursor={false} sequence={[commandlineEntry]} />)
			return newState
		})
	}

	useEffect(() => {
		const interval = setInterval(addLog, 2000)
		return () => clearInterval(interval)
	}, [])

	return (
		<>
			<section className="perspective-1000 overflow-hidden" onMouseMove={mouseMoveHandler}>
				<div className="h-screen transform-style-3d">
					<div
						className={cn(`absolute -inset-48 bg-cover bg-center bg-city-wallpaper`)}
						style={{
							transform: `rotateX(${(mousePosition.y - window.innerWidth / 2) * -0.005}deg) rotateY(${(mousePosition.x - window.innerHeight / 2) * -0.01}deg) translateZ(${0}rem)`,
						}}
					></div>
					<div
						className="absolute inset-0 bg-cover bg-center rotate-y-2 flex items-center justify-center"
						style={{
							transform: `rotateX(${(mousePosition.y - window.innerWidth / 2) * -0.005}deg) rotateY(${(mousePosition.x - window.innerHeight / 2) * -0.01}deg) translateZ(${10}rem)`,
						}}
					>
						<LogotypeBanner />
					</div>
					<div
						className="absolute inset-0 mt-40 bg-cover bg-center rotate-y-2 flex items-center justify-center gap-4"
						style={{
							transform: `rotateX(${(mousePosition.y - window.innerWidth / 2) * -0.005}deg) rotateY(${(mousePosition.x - window.innerHeight / 2) * -0.01}deg) translateZ(${5}rem)`,
						}}
					>
						workspace
						<ul className="max-w-96 marker:text-constructive list-disc list-inside bg-card/50 backdrop-blur-sm rounded-b-md w-full mt-0.5 px-2 divide-y-2">
							{commandlineEntries.map((commandlineEntry, index) => (
								<li key={index}>{commandlineEntry}</li>
							))}
						</ul>
					</div>
					<div
						className="absolute inset-0 mt-40 bg-cover bg-center rotate-y-2 flex items-center justify-center gap-4"
						style={{
							transform: `rotateX(${(mousePosition.y - window.innerWidth / 2) * -0.005}deg) rotateY(${(mousePosition.x - window.innerHeight / 2) * -0.01}deg) translateZ(${5}rem)`,
						}}
					></div>
				</div>
			</section>
		</>
	)
}

export default HomePage
